import React, { useEffect } from 'react'
import { LinkContainer } from 'react-router-bootstrap'
import { Button, Table } from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import Message from '../components/Message'
import Loader from '../components/Loader'
import { listOrders } from '../actions/orderActions'

const AllOrdersScreen = ({ history }) => {
  const dispatch = useDispatch()
  const orderList = useSelector((state) => state.ordersList)
  const { loading, error, orders } = orderList
  const userLogin = useSelector((state) => state.userLogin)
  const { userInfo } = userLogin
  const orderDeliver = useSelector((state) => state.orderDeliver)
  const {
    loading: loadingOrderDeliver,
    error: errorOrderDeliver,
    success,
  } = orderDeliver

  useEffect(() => {
    if (!userInfo) {
      history.push('/login')
    }
    if (userInfo && userInfo.admin) {
      dispatch(listOrders())
    } else {
      history.push('/login')
    }
  }, [dispatch, history, userInfo, success])

  return loadingOrderDeliver ? (
    <Loader />
  ) : errorOrderDeliver ? (
    <Message>{errorOrderDeliver}</Message>
  ) : (
    <>
      <h1>Orders</h1>
      {loading ? (
        <Loader />
      ) : error ? (
        <Message>{error}</Message>
      ) : (
        <Table striped bordered hover responsive className='table-sm'>
          <thead>
            <tr>
              <th>ID</th>
              <th>Shipping Address</th>
              <th>User</th>
              <th>User Email</th>
              <th>Placed at</th>
              <th>Total Price</th>
              <th>Payment Status</th>
              <th>Delivery Status</th>
              <th>Admin Ops</th>
            </tr>
          </thead>
          <tbody>
            {orders.map((order) => (
              <tr key={order._id}>
                <td>#{order._id}</td>
                <td>
                  {order.shippingAddress.address} - {order.shippingAddress.city}
                  - {order.shippingAddress.state}-{' '}
                  {order.shippingAddress.postalCode} -{' '}
                  {order.shippingAddress.country}
                </td>
                <td>{order.user && order.user.name}</td>
                <td>
                  <a href={`mailto:${order.user.email}`}>{order.user.email}</a>
                </td>
                <td>{order.createdAt.substring(0, 10)}</td>
                <td>{order.totalPrice}</td>
                <td>
                  {order.isPaid ? (
                    order.paidAt.substring(0, 10)
                  ) : (
                    <i className='fas fa-times' style={{ color: 'red' }}></i>
                  )}
                </td>
                <td>{order.delivered && order.deliveredAt.substring(0, 10)}</td>
                <td>
                  <LinkContainer to={`/admin/order/${order._id}/edit`}>
                    <Button variant='secondary' className='btn-sm m-2'>
                      <i className='fas fa-edit'></i>
                    </Button>
                  </LinkContainer>
                </td>
              </tr>
            ))}
          </tbody>
        </Table>
      )}
    </>
  )
}

export default AllOrdersScreen
